@import url(../../modules/icon/style.css);
.mt13 {
  margin-top: 13px; }

.mt60 {
  margin-top: 60px; }

.ml50 {
  margin-left: 50px; }

.ml16 {
  margin-left: 16px; }

.main-content-w {
  width: 1200px;
  margin: 0 auto; }

.ftheme-color {
  color: #0051ad; }

.fbtn-color {
  color: #ff7200 !important; }

.bg-theme-color {
  background-color: #0051ad !important; }

.float-l {
  float: left; }

.float-r {
  float: right; }

.clearfix {
  clear: both; }

.clearfix:after {
  display: block;
  content: ".";
  clear: both;
  font-size: 0;
  height: 0;
  visibility: hidden; }

ul,
ol,
li,
dl,
dt,
dd,
label {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  outline: none;
  margin: 0;
  padding: 0; }

a:link {
  text-decoration: none; }

a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

a:active {
  text-decoration: none; }

.cart-empty {
  margin-top: 120px; }
  .cart-empty h2 {
    font-size: 35px;
    color: #7a7a7a;
    font-weight: 400;
    line-height: 250px;
    text-align: center; }
    .cart-empty h2 i {
      display: inline-block;
      padding: 0 30px;
      font-size: 74px;
      line-height: 250px;
      vertical-align: middle;
      color: #b7b7b7; }
  .cart-empty button {
    display: block;
    width: 176px;
    margin: 0 auto;
    font-size: 14px;
    height: 36px;
    line-height: 36px;
    color: #fff;
    background: #ff7200;
    text-align: center;
    border-radius: 3px;
    letter-spacing: 10px; }

[v-cloak] {
  display: none; }

section {
  float: left;
  width: 282px; }

.left-fault-query {
  border: 1px solid #203a6d;
  background: url(imgs/personal-left-bg.jpg) no-repeat center;
  height: 313px; }
  .left-fault-query img {
    display: block;
    height: 102px;
    width: 102px;
    border: 2px solid #fff;
    margin: 35px auto 20px;
    border-radius: 50%; }
  .left-fault-query p {
    font-size: 14px;
    color: #fff;
    line-height: 36px;
    text-align: center; }

.left-nav {
  margin-bottom: 50px; }

.left-nav > li > a {
  display: block;
  height: 48px;
  padding-left: 40px;
  font-size: 18px;
  font-weight: 400;
  line-height: 48px;
  color: #303030;
  background-color: #f8f8f8;
  border: 1px solid #e9e9e9;
  border-top: none; }
  .left-nav > li > a i {
    display: inline-block;
    height: 32px;
    width: 32px;
    margin-right: 20px;
    font-size: 20px;
    color: #ff7200;
    line-height: 32px;
    text-align: center; }
  .left-nav > li > a .icon-order {
    font-size: 22px; }
  .left-nav > li > a .icon-gouwuche2 {
    font-size: 22px; }

.left-subnav {
  display: block;
  border-left: 1px solid #e9e9e9;
  border-right: 1px solid #e9e9e9; }
  .left-subnav li {
    line-height: 48px;
    background-color: #fff;
    border-bottom: 1px solid #e9e9e9; }
  .left-subnav a {
    position: relative;
    display: block;
    padding-left: 92px;
    font-size: 14px;
    line-height: 48px;
    color: #303030; }
  .left-subnav i {
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 12px; }
  .left-subnav li:hover,
  .left-subnav a:hover,
  .left-subnav .select a {
    background-color: #ff7200;
    color: #fff; }

.right-content {
  margin-left: 296px;
  min-height: 800px; }

.common-title {
  width: 98%;
  height: 38px;
  padding-left: 2%;
  font-size: 16px;
  line-height: 38px;
  color: #303030;
  background-color: #f8f8f8;
  border: 1px solid #e9e9e9; }



